import {
    CircleWrap, TitleWrap, BoldSpan, TitleSize,
    SubSize, SubMargin, BasicWrap, ProfilePhotoWrap,
    ProfilePhoto, ProfilePhotoSubWrap,
    ProfileEduWrap, ProfileEduDate,
    TabBtnWrap, TabBtn
} from './css.js';
import { useState, useEffect } from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import './App.css';

// 
let TabUI = {
  info : <Information />,
  license : <License />
}

// export default()
function Introduce() {
  let [fade, setFade] = useState('');

  useEffect(()=>{
    setTimeout(()=>{setFade('end')}, 100);
    return () => {
      setFade('')
    }
  }, [])

  return (
    <Content fade={fade}/>
  )
}

function Content({fade}) {
  return (
    <div className={`start ${fade}`}>
      <div style={{marginBottom: '100px'}}>
        <CircleWrap>
            <div>
              <img src={process.env.PUBLIC_URL + '/circle1.png'} style={{width: '200px', height: '200px'}}></img>
            </div>
            <div>
              <img src={process.env.PUBLIC_URL + '/circle2.png'} style={{width: '100px', height: '100px'}}></img>
            </div>
            <TitleWrap>
              <TitleSize> <BoldSpan></BoldSpan> <BoldSpan></BoldSpan></TitleSize>
              <TitleSize>  .</TitleSize>
            </TitleWrap>
        </CircleWrap>
        <SubSize>
          <SubMargin>,      .</SubMargin>
          <SubMargin>         </SubMargin>
          <SubMargin>,        .</SubMargin>
        </SubSize>
      </div>

      <TabContent></TabContent>
    </div>
  )
}

function TabContent() {
  let [present, setPresent] = useState('info');
  let [activeTab, setActiveTab] = useState('info');

  const ClickBtn = (tab) => {
    setActiveTab(tab);
  }

  return (
    <>
      <TabBtnWrap>
        <TabBtn 
          active={activeTab === 'info'}
          onClick={() => {
            setPresent('info');
            ClickBtn('info')
          }}
        > / </TabBtn>
        <TabBtn 
          active={activeTab === 'license'}
          onClick={() => {
            setPresent('license');
            ClickBtn('license')
          }}
        ></TabBtn>
      </TabBtnWrap>
      <BasicWrap>
        {TabUI[present]}
      </BasicWrap>
    </>
  )
}

